import React from 'react';
import styled from '@emotion/styled';
import {animated, useTransition} from 'react-spring';
import useMeasure from './measure';
import Result from './result';

const List = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  width: 100%;
`;

function SpringContainer({spring, children}) {
  const [bind, {height}] = useMeasure();

  return (
    <animated.div
      style={{
        opacity: spring,
        height: spring.interpolate({range: [0, 1], output: [0, height]}),
      }}
    >
      <animated.div {...bind}>{children}</animated.div>
    </animated.div>
  );
}

export default function({results, selection}) {
  const transitions = useTransition(results, item => item.ref, {
    from: {spring: 0},
    enter: {spring: 1},
    leave: {spring: 0},
  });

  return (
    <List>
      {transitions.map(({key, item, props: {spring}}, index) => (
        <SpringContainer key={key} spring={spring}>
          <Result selected={index === selection} result={item} />
        </SpringContainer>
      ))}
    </List>
  );
}
